import React, { useCallback, useEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, FormControl, FormLabel, Input, VStack,
  Button,
  RadioGroup,
  Radio
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { cardPayload } from '../../../../types/card';
import { updateCardPayload } from '../../../../store/actionCreators/card';
import CurrencyInput from '../../../../components/CurrencyInput';
import { useDrawer } from '../../../../hooks/drawer';

const CardCreateAddress = React.lazy(() => import('../CardAddress/CardAddress'));

const CardInfo = () => {
  const { t } = useTranslation();
  const { open, goBack } = useDrawer();
  const dispatch = useAppAsyncDispatch();

  const cardPayloadData = useAppSelector((store) => store.card.cardPayload, shallowEqual);
  const replaceCardId = useAppSelector((store) => store.card.replaceCardId);
  const selectedAccount = useAppSelector((store) => store.account.selectedAccount, shallowEqual);

  const setPayload = useCallback((key: keyof cardPayload, value: any) => {
    if (cardPayloadData) {
      const copiedData:cardPayload = {
        ...cardPayloadData,
        [key]: value
      };
      dispatch(updateCardPayload(copiedData));
    }
  }, [cardPayloadData]);

  const isValid = useMemo(() => cardPayloadData?.limitInterval && cardPayloadData?.label?.length !== 0 && cardPayloadData?.limitAmount, [cardPayloadData]);

  useEffect(() => {
    if (!cardPayloadData || !cardPayloadData.limitAmount || cardPayloadData.limitAmount === '') {
      setPayload('limitAmount', '0.00');
    }
  }, [cardPayloadData]);

  return (
    <div className="page">
      <MiniBox
        onBack={goBack}
        heading={t(replaceCardId ? `create_card_info.replace_title_${cardPayloadData?.cardType}` : `create_card_info.title_${cardPayloadData?.cardType}`)}
        footer={(
          <Button width="100%" variant="primary" disabled={!isValid} onClick={() => open(CardCreateAddress)}>
            {t('create_card_info.ctaText')}
          </Button>
)}
      >
        <Box width="100%" paddingTop="15px">
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('create_card_info.cardLabel')}
            </FormLabel>
            <Input
              value={cardPayloadData?.label}
              variant="primary"
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setPayload('label', e.target.value)}
            />
          </FormControl>
          {cardPayloadData?.cardType === 'physical'
            ? (
              <>
                <FormControl variant="primary">
                  <FormLabel variant="primary">
                    {t('create_card_info.embossingPerson')}
                  </FormLabel>
                  <Input
                    maxLength={21}
                    value={cardPayloadData?.embossingPerson}
                    variant="primary"
                    onChange={(e: React.ChangeEvent<HTMLInputElement>) => setPayload('embossingPerson', e.target.value)}
                  />
                </FormControl>
                {selectedAccount?.businessId !== ''
                  ? (
                    <FormControl variant="primary">
                      <FormLabel variant="primary">
                        {t('create_card_info.embossingBusiness')}
                      </FormLabel>
                      <Input
                        maxLength={21}
                        value={cardPayloadData?.embossingBusiness}
                        variant="primary"
                        onChange={(e: React.ChangeEvent<HTMLInputElement>) => setPayload('embossingBusiness', e.target.value)}
                      />
                    </FormControl>
                  ) : null }
              </>
            ) : null }
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('create_card_info.setSpendingLimit')}
            </FormLabel>
            <CurrencyInput
              value={cardPayloadData?.limitAmount || ''}
              onChange={(value) => {
                setPayload('limitAmount', value);
              }}
            />
          </FormControl>

          <RadioGroup onChange={(v:string) => v && setPayload('limitInterval', v)} value={cardPayloadData?.limitInterval}>

            <VStack alignItems="flex-start" py="24px">
              <Radio value="daily" variant="primary">
                <Text variant="regular" fontSize="14px">{t('create_card_info.daily')}</Text>
              </Radio>

              <Radio value="weekly" variant="primary">
                <Text variant="regular" fontSize="14px">{t('create_card_info.perWeek')}</Text>
              </Radio>

              <Radio value="monthly" variant="primary">
                <Text variant="regular" fontSize="14px">{t('create_card_info.perMonth')}</Text>
              </Radio>

              <Radio value="yearly" variant="primary">
                <Text variant="regular" fontSize="14px">{t('create_card_info.perYear')}</Text>
              </Radio>

              <Radio value="allTime" variant="primary">
                <Text variant="regular" fontSize="14px">{t('create_card_info.allTime')}</Text>
              </Radio>

              <Radio value="perTransaction" variant="primary">
                <Text variant="regular" fontSize="14px">{t('create_card_info.perTransaction')}</Text>
              </Radio>

            </VStack>
          </RadioGroup>

          {/* <Box>
            <Text variant="info" pb="16px">{t('create_card_info.allowBlockcategories')}</Text>
            <Box className={style.categories}>
              <HStack className={style.category}>
                <Box>
                  <Text variant="branding">{t('create_card_info.allowedCategories')}</Text>
                  <Text variant="info">{t('create_card_info.allCategories')}</Text>
                </Box>
                <Text className={style.edit}>{t('create_card_info.edit')}</Text>
              </HStack>
              <HStack className={style.category}>
                <Box>
                  <Text variant="branding">{t('create_card_info.blockedCategories')}</Text>
                  <Text variant="info">{t('create_card_info.none')}</Text>
                </Box>
                <Text className={style.edit}>{t('create_card_info.edit')}</Text>
              </HStack>
            </Box>
          </Box> */}

        </Box>
      </MiniBox>

    </div>
  );
};

export default CardInfo;
